	module.exports = {"data":{"markdownRemark":{"html":"<h2>Algorithms</h2>\n<p>An algorithm is like a recipe. It’s a series of steps one takes to achieve a specific outcome.</p>\n<p>Some common types of algorithms include sorting algorithms, search algorithms, and compression algorithms. Classes of algorithms include Graph, Dynamic Programming, Sorting, Searching, Strings, Math, Computational Geometry, Optimization, Miscellaneous. Although technically not a class of algorithms, Data Structures is often grouped with them. </p>\n<h3>Efficiency</h3>\n<p>Algorithms are most commonly judged by their efficiency, the amount of computing resources they require to complete their task.</p>\n<h3>Sorting Algorithms</h3>\n<p>Sorting algorithms comes in various flavors depending on your necessity.\nSome, very common and widely used are:</p>\n<h4>Quick Sort</h4>\n<p>There is no sorting discussion which can finish without quick sort. Basic idea is in link below.\n<a href=\"http://me.dt.in.th/page/Quicksort/\">Quick Sort</a></p>\n<p>freeCodeCamp’s curriculum heavily emphasizes creating algorithms. This is because learning algorithms is a good way to practice programming skills. Interviewers most commonly test candidates on algorithms during developer job interviews.</p>\n<h3>Further Resources</h3>\n<p><a href=\"https://www.youtube.com/watch?v=rL8X2mlNHPM\">Intro to Algorithms | Crash Course: Computer Science</a></p>\n<p>This video gives an accessible and lively introduction to algorithms focusing on sorting and graph search algorithms. </p>\n<p><a href=\"https://www.youtube.com/watch?v=CvSOaYi89B4\">What is an Algorithm and Why Should you Care? | Khan Academy</a></p>\n<p>This video introduces algorithms and briefly discusses some high profile uses of them. </p>\n<p><a href=\"https://www.youtube.com/watch?v=kPRA0W1kECg\">15 Sorting Algorithms in 6 Minutes | Timo Bingmann</a></p>\n<p>This video visually demonstrates some popular sorting algorithms that are commonly taught in programming and Computer Science courses.</p>\n<p><a href=\"http://algo-visualizer.jasonpark.me\">Algorithm Visualizer</a></p>\n<p>This is also a really good open source project that helps you visualize algorithms. </p>","fields":{"slug":"/algorithms/"},"frontmatter":{"title":"Algorithms"}}},"pathContext":{"slug":"/algorithms/"}}

